import React,{useState,useEffect} from 'react'
import {Route,useRouteMatch} from 'react-router-dom'
import { AddProduct } from './AddProduct';
import AllProducts from './AllProducts';
import { EditProduct } from './EditProduct';
import { Sidebar } from './SideBar';
import Users from './Users';
import { Card, CardBody, CardFooter, CardHeader } from 'reactstrap';

const DashBoard=()=>{
    const [products,setProducts]=useState([]);
    const [users,setUsers]=useState([]);


    useEffect(()=>{
        fetch('http://localhost:3002/api/products')
        .then(res=>res.json())
        .then(data=>{
            setProducts(data);
        })
        fetch('http://localhost:3002/api/users')
        .then(res=>res.json())
        .then(data=>{
            setUsers(data);
        }
        )
    },[])
    const {url}=useRouteMatch();
        return(
            <div className="container-fluid">
                <div className="row">
                    <div className="col-md-2">
                        <Sidebar url={url}/>
                    </div>
                <div className="col-sm-10">
                    <Route path={`${url}/newproduct`}>
                       <AddProduct url={url}/>
                    </Route>
                    <Route path={`${url}/products`}>
                       <AllProducts/>
                    </Route>
                    <Route exact path={`${url}/users`}>
                        <Users/>
                    </Route>
                    <Route path={`${url}/product/:id`}>
                        <EditProduct/>
                        </Route>
                    <Route exact path={`${url}`}>
                       <div className="container">
                            <h1>Dashboard</h1>
                            <p>Welcome to the Dashboard</p>
                            <div className="row">
                                <div className="col-md-6">
                                    <Card title="Products" color="success">
                                        <CardHeader>
                                            <h2>Products</h2>
                                        </CardHeader>
                                        <CardBody>
                                            <h3>{products.length}</h3>
                                        </CardBody>
                                        <CardFooter>
                                            <p>You can add new products and edit existing products</p>
                                            <a href={`${url}/products`}>View Products</a>
                                        </CardFooter>
                                    </Card>
                                </div>
                                <div className="col-md-6">
                                    <Card title="Users" color="info">
                                        <CardHeader>
                                            <h2>Users</h2>
                                        </CardHeader>
                                        <CardBody>
                                            <h3>{users.length}</h3>
                                        </CardBody>
                                        <CardFooter>
                                            <p>You can view all the users</p>
                                            <a href={`${url}/users`}>View Users</a>
                                        </CardFooter>
                                    </Card>
                                </div>
                               
                            </div>
                       </div>
                    </Route>
                </div>
            </div>
            </div>
        )
}
export default DashBoard;